import React, { PureComponent } from 'react'

import Layout from '../Layout';
import Container from '../../components/Container'
import Button from '../../components/Button'
import VerticalCenter from '../../components/VerticalCenter'
import Link from '../../components/Link'

import withReducer from '../QuestionPage/hoc'

import { storeAnswers } from '../../services/google-sheets';

class Destination extends PureComponent {
  componentDidMount() {
    const { answers, startTimes } = this.props;
    const now = new Date();
    const { size } = startTimes;
    const timeData = startTimes.reduceRight((all, thisPageStart, qId) => {
      const answer = answers[qId];
      const nextPageStart = qId === size - 1 ? now : startTimes.get(qId + 1);
      all[qId] = {
        answerTime: answer ? answer[2] - thisPageStart : '',
        nextTime: answer ? nextPageStart - answer[2] : nextPageStart - thisPageStart,
      };
      return all;
    }, []);
    storeAnswers(answers.map((a, i) => [a[0], a[1], timeData[i].answerTime, timeData[i].nextTime])).then(console.log);
  }

  render() {
    return (
      <Layout>
        <VerticalCenter>
          <Container textAlign="center">
            <Button is={Link} to="/result">看結果</Button>
          </Container>
        </VerticalCenter>
      </Layout>
    );
  }
}

export default withReducer(Destination)
